.App {
    color: orange;
    display: flex;
    flex-direction: row;
}

.player-list {
    display: inline;
    margin: 10px;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
}

.player-list:hover {
    animation: colorChenger 2s linear infinite;
}

@keyframes fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }

}

.graph {
    display: flex;
    flex-wrap: wrap;
    animation: fade-in 2s;
    justify-content: center;
}

.statistic-container {
    margin: 5px;
}

